.App,
body,
html {
  text-align: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Quote {
  min-height: 80vh;
  width: 75vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Quote-Main {
  width: 100%;
  font-family: 'Courier New', Courier, monospace;
}

.Quote-Buttons {
  margin: 0.5rem auto;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.Tweet-Button {
  color: white;
  text-decoration: none;
}

.Footer {
  /* position: absolute; */
  width: 100%;
  /* bottom: 0px; */
  color: white;
  font-size: 0.6rem;
  border-top: 1px solid white;
}

.Footer p {
  padding-bottom: 0.3rem;
}

.Footer a {
  color: orange;
  text-decoration: none;
}